import axios from 'axios';
//import { HTTP } from '@/http-common'

const mainUrl="https://dtl2020.herokuapp.com/getdics?header="
const getPetsUrl="https://dtl2020.herokuapp.com/pets"
const getPhotosUrl="http://dtl2020.herokuapp.com/photos?petid=";

class connect {
    static baseStaticField =[];
    static originalPets =[];
   // static fields=['sf'];  
    static async getDictionary(dictName) {
        let data={}
        await axios.get(mainUrl+dictName).then((response)=> {
            data=response
        }).catch((err)=> {
            data =err.response
        })
        return data
    } 
     static async getPhotos(id) {
        let data={}
        await axios.get(getPhotosUrl+id).then((response)=> {
            data=response
        }).catch((err)=> {
            data =err.response
        })
        return data
    } 
    static async fieldsSet() {
        return connect.baseStaticField;
    }
    static async getPets() {
        let data={}
        await axios.get(getPetsUrl).then((response)=> {
            data=response
        }).catch((err)=> {
            data =err.response
        })
     
        let resultAr= data.data.map((pet)=> {
            let newPet={};
            pet.petData.forEach((element) => {
                newPet[element.cellIndex] =[ element.cellValue,element.cellHeader , pet._id];
                connect.baseStaticField[element.cellIndex]={ key: String(element.cellIndex),
                    label: element.cellHeader,
                    sortable: true,}
            })
            
            return newPet
        })
    
        console.log(connect.baseStaticField);
        return resultAr
    }
}


export default connect;